import axios from '../../Api/axios';
import React, { useEffect, useState } from 'react'
import { FaTimes } from 'react-icons/fa'
import '../Home/Popup.css'
import PaystackPop from "@paystack/inline-js";



const Myvote = ({ setVote, setMain, setClick, fullname, craft, id, email }) => {
  
  const amount = 10000;


  const handleVote = (e) => {
    e.preventDefault();
    const paystack = new PaystackPop();

    paystack.newTransaction({
      key:"pk_live_5e3c204d636150b44f8962627e1c04a36c1189ca",
      // key: "pk_test_e78da199af88d830aadeaeec3586586193c825d3",
      amount: amount,
      email,
      onSuccess() {
        axios.post('/api/payment', { id })
        .then ((response) =>{
          // console.log(response)
        })
      },
    });
};


  return (
      <>
                 <div className=' popup text-center'>
       
       <div className=' popup text-center p-3 '>
<div className='popup-inner'>
                      <button className=' btn close-btn' onClick={() => { setVote(false); setMain(true);setClick(false) }}> <FaTimes /></button>

<div className="modal-body" >
<h5 className="modal-title blue" style={{ fontWeight: 700 }}>Participant</h5>
</div>
      <div>
        <h5 className=' blue' style={{ fontWeight:500 }}>{ fullname }</h5>
        <h6 className='  text-warning' style={{ fontWeight:500 }}>{ craft }</h6>

        </div>
        <div>
    <form action="" className="  text-left" onSubmit={handleVote}>
     <div className="col-md-12">
     <div className="row form-group text-left ">
  <input type="email" className="form-control  text-left " value={email} disabled  required/>
              </div>
              <div className="row form-group text-left ">
 <input type="text"  className="  text-left form-control" disabled value="100 Naira"  />
              </div>
              <div className="row form-group text-left ">
              
                <input
                  className="btn mt-2 btn-success"
                   type="submit"                         
              value='Pay Now'                    
                />
                </div>
            </div>
          </form>
                      </div>
                      </div>
      </div>
                </div>

      
      </>
  )
}

export default Myvote