import React, { useEffect } from 'react'
import { useState } from 'react'
import {   FaLongArrowAltDown, FaTimes } from 'react-icons/fa'
// import './Upload.css'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import UploadPicture from './UploadPicture'
import UploadVideo from './UploadVideo'
import './SlideIn.css'

import { useLocation } from 'react-router-dom';
import axios from '../../Api/axios';
import Uploaded from './Uploaded';
import logo from "../assets/images/logow.png";


const Upload = ({picture,videoOne,videoTwo,name,token, setUpload,stagename,setMain,youtubeone_link,youtubetwo_link}) => {
  

  const [formone,setFormone] = useState(false)
  const [formtwo,setFormtwo] = useState(false)
  const [header, setHeader] = useState(true)
 
 

  const goBack = () => {
    window.history.back()
  }
  const handleSecondForm = () =>{
    setFormtwo(true)
    setFormone(false)
  }


  const Header = () => {
  return (
    <div className="App">
    <div className="form-container text-center">
    <div className='p-4'>
    <h2 className="main-heading text-dark mt-3">Welcome <span className="text-info">{name}</span> <br/> Participant File Upload Session</h2>
      <p className="white text-center">You are Expected to upload your Picture and a <span className='text-danger'>60sec video.</span> Once your files are uploaded, you may not be able to upload again</p> 
      <button className='btn call_to-btn btn-primary' onClick={() => { setFormone(true); setHeader(false) }}>Ready to Go <FaLongArrowAltDown/></button>        
        </div>
      </div>
      </div>
  )
  }
  

  if (picture) {
    return (
      <section className="contact_section pt-4 bgblue pb-5">
      <div className="container">
          <div className="row justify-content-center">
          <div className="col-lg-6 col-md-12 text-center mt-3">
              <div className="site-logo ">
                <img src={logo} alt="site logo" className="logo" />
              </div>
</div>
                
              <div className="col-lg-7 col-md-12">
                <div className="card shadow-lg border-0 rounded-lg mt-5 ">
                  <div className="text-right p-3">
                    <button
                      className=" btn"
                    style={{ fontSize: 20 }}
                  onClick={() => { setUpload(false); setMain(true)}}
                    
                    >
                      {" "}
                      <FaTimes />
                    </button>
                </div>
                <ToastContainer />
                
               
                  <UploadVideo  LinkOne={youtubeone_link} LinkTwo={youtubetwo_link}  />
               
              
                </div>
            </div>
            
          </div>
          <br/>
            <br/>
          </div>
      </section>
      
              

    )
  }

return (
  
  <>
    <section className="contact_section pt-4 bgblue pb-5">
          <div className="container">
        <div className="row justify-content-center">
        <div className="col-lg-6 col-md-12 text-center mt-3">
              <div className="site-logo ">
                <img src={logo} alt="site logo" className="logo" />
              </div>
</div>
                
              <div className="col-lg-7 col-md-12">
                <div className="card shadow-lg border-0 rounded-lg mt-5">
                  <div className="text-right p-3">
                    <button
                      className=" btn"
                      style={{ fontSize: 20 }}
                  onClick={() => { setUpload(false); setMain(true)}}
                    >
                      {" "}
                      <FaTimes />
                    </button>
                  </div>
            
                  <ToastContainer />

          
             
                  {header ? <Header /> : " "}
                  {formone && <UploadPicture token={token} setFormtwo={handleSecondForm} />}
                  {formtwo && <UploadVideo LinkOne={youtubeone_link} LinkTwo={youtubetwo_link}  />}
                  

             
              
                </div>
              </div>
            </div>
          </div>
        </section>
              
  </>

       )
}

export default Upload